<template>
  <view class="nvue-page-root">
    <view class="uni-common-mt">
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>车牌号</text></view>
          <view class="uni-input-wrapper">
            <input class="uni-input" v-model="params.licensePlate" placeholder="请输入" />
          </view>
        </view>
        <view class="uni-form-item uni-column uni-list">
          <view class="uni-list-cell">
            <view class="title"><text class="uni-form-item__title"><text>*</text>汽车品牌</text></view>
            <view class="uni-list-cell-db">
              <picker @change="bindPickerChange" :value="brandIndex" :range="brandArray" range-key="name">
                <view class="uni-input">{{brandArray[brandIndex].name}}</view>
              </picker>
            </view>
          </view>
        </view>
        <view class="uni-form-item uni-column uni-list">
          <view class="uni-list-cell">
            <view class="title"><text class="uni-form-item__title"><text>*</text>汽车型号</text></view>
            <view class="uni-list-cell-db">
              <picker @change="bindModelPickerChange" :value="modelIndex" :range="modelArray" range-key="model">
                <view class="uni-input">{{modelArray[modelIndex].model}}</view>
              </picker>
            </view>
          </view>
        </view>
        <view class="uni-form-item uni-column uni-list">
          <view class="uni-list-cell">
            <view class="title"><text class="uni-form-item__title"><text>*</text>车险投保公司</text></view>
            <view class="uni-list-cell-db">
              <picker @change="bindCompanyPickerChange" :value="companyIndex" :range="companyArray" range-key="name">
                <view class="uni-input">{{companyArray[companyIndex].name}}</view>
              </picker>
            </view>
          </view>
        </view>
        </view>
        <view class="submit" @click="saveInfo()">
          <button> 确 定 </button>
        </view>
    </view>
</template>
<script>
  export default {
    data() {
      return {
        title: 'input',
        brandIndex: 0,
        modelIndex: 0,
        companyIndex: 0,
        inputValue: '',
        modelId: null, // 汽车型号ID
        companyId: null, // 保险公司ID
        logo: null,
        brandChange: true,  
				brandArray: [],  // 车辆品牌数据
				modelArray: [],  // 车辆型号数据
				companyArray: [], // 保险公司数据
        listArray: [], // 车辆所有信息集合
        params: {
          name: null,
          phone: null,
          emergencyPhone: null,
          licensePlate: null,
        }
      }
    },
    onLoad(options){
      let _that = this
      _that.$http.treeList().then(res=>{
        this.brandArray = res.data
        this.listArray = res.data
        this.modelArray = res.data[0].children
        this.modelId = res.data[0].children[0].id
        this.logo = res.data[0].logo
      })
      _that.$http.companyList().then(res=>{
        this.companyArray = res.data
        this.companyId = res.data[0].id
      })
      console.log('options =>',options)
      if(JSON.stringify(options) !== '{}'){
        let userInfo = JSON.parse(options.params)
        console.log(JSON.parse(options.params))
        console.log(userInfo)
        this.params.emergencyPhone = userInfo.emergencyPhone
        this.params.name = userInfo.name
        this.params.phone = userInfo.phone
      }
      
    },
    methods: {
      // 选择车辆品牌
      bindPickerChange: function(e) {
				this.brandIndex = e.detail.value
        console.log(this.listArray)
        this.modelArray = this.listArray[this.brandIndex].children
        this.modelId = this.listArray[this.brandIndex].children[this.modelIndex].id
        this.logo = this.listArray[this.brandIndex].logo
			},

      // 选择汽车型号
      bindModelPickerChange: function(e) {
				this.modelIndex = e.detail.value
        this.modelId = this.listArray[this.brandIndex].children[this.modelIndex].id
        console.log('modelId =>',this.modelId)
        console.log('brandIndex =>',this.brandIndex)
			},

      // 选择保险公司
      bindCompanyPickerChange: function(e) {
				this.companyIndex = e.detail.value
        this.companyId = this.companyArray[this.companyIndex].id
			},
      
      // 保存
      saveInfo: function(){
        let data = this.params
        if(data.licensePlate == null){
          uni.showToast({
            title:"车牌号不能为空" ,
            icon:'none',
            duration: 2000
          })
        }else if(this.modelId == null){
          uni.showToast({
            title:"汽车型号不能为空" ,
            icon:'none',
            duration: 2000
          })
        }else if(this.companyId == null){
          uni.showToast({
            title:"车险投保公司不能为空" ,
            icon:'none',
            duration: 2000
          })
        }else{
          data.insuranceId = this.companyId
          data.carBrandId = this.modelId
          data.logo = this.logo
          console.log('data=>',data)
          this.$http.carOwnerAdd(data).then(res=>{
            console.log(res)
            uni.navigateBack({
              url: '/pages/rescue/rescueAdd'
            })
          })
        }
        
      }

    },
  }
</script>

